<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>船舶中心</BreadcrumbItem>
      <BreadcrumbItem>船舶详情</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>船舶信息</span>
          <Row slot="content" class="row">
              <Col span="2">船名</Col>
              <Col span="2">{{result.shipnameCn ? result.shipnameCn : result.shipnameEn ? result.shipnameEn:'-'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船东</Col>
              <Col span="6">{{result.contactName ? result.contactName : "-"}}</Col>
              <Col span="2">手机号</Col>
              <Col span="6">{{result.contactPhone ? result.contactPhone : "-"}}</Col>
          </Row>
          <Row slot="content" class="row">
              <Col span="2">mmsi</Col>
              <Col span="2">{{result.mmsi ? result.mmsi : "-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">船长</Col>
              <Col span="6">{{result.length ? result.length:'-'}}</Col>
              <Col span="2">船宽</Col>
              <Col span="6">{{result.breadth ? result.breadth:'-'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">载货吨位</Col>
              <Col span="6">{{result.loadTons ? result.loadTons :'-'}}</Col>
              <Col span="2">TPC</Col>
              <Col span="6">{{result.tpc ? result.tpc :"-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">证书吨位</Col>
              <Col span="6">{{result.certTons ? result.certTons:'-'}}</Col>
              <Col span="2">每公里油耗</Col>
              <Col span="6">{{result.fuelConsumptionPerKm ? result.fuelConsumptionPerKm :'-'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="2">历史航线</Col>
              <Col span="10">
                 <span v-for="(list,i) in hisLine" :key="i">{{list}}</span>
              </Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
               <Button type="primary">
                <router-link :to="{
                 path: '/shipCenter/updateship',query:{
                   code:this.$route.query.code
                }
              }" style="color:#fff">修改船舶信息</router-link>
                </Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                hisLine:""
            }
        },
         methods:{
         async  shipDetail(){
              let data  = {
                shipCode:this.$route.query.code
              }
             try{
                   let res = await this.$http.shipDetail(data)
                   if(0==res.data.status ){
                     this.result = res.data.result
                     let hisLine = res.data.result.historyRoute
                     if(res.data.result.historyRoute){
                       let newLine =   hisLine.slice(1,hisLine.length-1)
                       let endLine = newLine.split(',')
                       this.hisLine = endLine
                     }else{
                        this.hisLine  = ''
                     }

                   }

              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }

           }
        },
        created(){
          this.shipDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
}

</style>



